<template>
  <div class="sec_main_panel sec_container">
    <el-backtop target=".sec_main_panel" :bottom="80"></el-backtop>
    <sec-header></sec-header>
    <div class="sec_main">
      <div style="display: flex; justify-content: center; flex-direction: column;">

        <div class="router-panel">
          <a :class="'router-a ' + (currentRouteName === 'home' ? 'ACTIVE' : '')" @click="onPanelChange('home')">{{$t('header.trade')}}</a>
          <a :class="'router-a ' + (currentRouteName === 'rank' ? 'ACTIVE' : '')" @click="onPanelChange('rank')">{{$t('header.rank')}}</a>
          <a :class="'router-a ' + (currentRouteName === 'token' ? 'ACTIVE' : '')" @click="onPanelChange('token')">{{dtokenSymbol}}</a>
          <a :class="'router-a ' + (currentRouteName === 'donate' ? 'ACTIVE' : '')" @click="onPanelChange('donate')">{{$t('header.donate')}}</a>
        </div>
      </div>
      <div class="sec_view_container">
        <router-view/>
      </div>
    </div>
    <sec-footer></sec-footer>
  </div>
</template>

<script>
import base from "@/utils/base"

const SecHeader = () => import('@/components/sec_header')
const SecFooter = () => import('@/components/sec_footer')
export default {
  mixins: [base],
  name: 'MainPanel',
  props: {
  },
  components: {
    SecHeader,
    SecFooter,
  },
  data() {
    return {
      panel: '',
    }
  },
  created() {
    this.selectToken = this.tokenAddress
    this.panel = this.$route.name
  },
  computed: {
    dtokenSymbol() {
      if (null === this.$store.state.dtoken) {
        return this.$t('header.token')
      } else {
        return this.$store.state.dtoken.symbol
      }
    },
  },
  watch: {
    '$route.name'() {
      this.panel = this.$route.name
    }
  },
  methods: {
    onPanelChange(val) {
      this.$router.push({
        name: val
      })
    },
  }
}
</script>

